.daterangepicker table{width:100%;margin:0;}
.daterangepicker td,
.daterangepicker th{text-align:center;width:20px;height:20px;cursor:pointer;white-space:nowrap;}
.daterangepicker td.off{color:#999;}
.daterangepicker td.disabled{color:#999;}
.daterangepicker td.available:hover,
.daterangepicker th.available:hover{background:#eee;color:#34495E;}
.daterangepicker td.in-range{background:#E4E7EA;-webkit-border-radius:0;-moz-border-radius:0;border-radius:0;}
.daterangepicker td.available + td.start-date{-webkit-border-radius:4px 0 0 4px;-moz-border-radius:4px 0 0 4px;border-radius:4px 0 0 4px;}
.daterangepicker td.in-range + td.end-date{-webkit-border-radius:0 4px 4px 0;-moz-border-radius:0 4px 4px 0;border-radius:0 4px 4px 0;}
.daterangepicker td.start-date.end-date{-webkit-border-radius:4px!important;-moz-border-radius:4px!important;border-radius:4px!important;}
.daterangepicker td.active,
.daterangepicker td.active:hover{background-color:#536A7F;color:#fff;}
.daterangepicker td.week,
.daterangepicker th.week{font-size:80%;color:#ccc;}
.daterangepicker select.monthselect,
.daterangepicker select.yearselect{font-size:12px;padding:1px;height:auto;margin:0;cursor:default;height:30px;border:1px solid #ADB2B5;line-height:30px;border-radius:0px!important;}
.daterangepicker select.monthselect{margin-right:2%;width:56%;}
.daterangepicker select.yearselect{width:40%;}
.daterangepicker select.hourselect,
.daterangepicker select.minuteselect,
.daterangepicker select.ampmselect{width:50px;margin-bottom:0;}
.daterangepicker_start_input{float:left;}
.daterangepicker_end_input{float:left;padding-left:11px;}
.daterangepicker th.month{width:auto;}
.daterangepicker .daterangepicker_start_input label,
.daterangepicker .daterangepicker_end_input label{color:#333;display:block;font-size:11px;font-weight:normal;height:20px;line-height:20px;margin-bottom:2px;text-shadow:#fff 1px 1px 0px;text-transform:uppercase;width:74px;}
.daterangepicker .ranges input{font-size:11px;}
.daterangepicker .ranges .input-mini{background-color:#eee;border:1px solid #ccc;border-radius:4px;color:#555;display:block;font-size:11px;height:30px;line-height:30px;vertical-align:middle;margin:0 0 10px 0;padding:0 6px;width:74px;}
.daterangepicker .ranges .input-mini:hover{cursor:pointer;}
.daterangepicker .ranges ul{list-style:none;margin:0;padding:0;}
.daterangepicker .ranges li{font-size:13px;background:#f5f5f5;border:1px solid #f5f5f5;color:#536A7F;padding:3px 12px;margin-bottom:8px;-webkit-border-radius:5px;-moz-border-radius:5px;border-radius:5px;cursor:pointer;}
.daterangepicker .ranges li.active,
.daterangepicker .ranges li:hover{background:#536A7F;border:1px solid #536A7F;color:#fff;}
.daterangepicker .calendar{display:none;max-width:270px;}
.daterangepicker.show-calendar .calendar{display:block;}
.daterangepicker .calendar.single .calendar-date{border:none;}
.daterangepicker.single .ranges,
.daterangepicker.single .calendar{float:none;}
.daterangepicker .ranges{width:160px;text-align:left;margin:4px;}
.daterangepicker .ranges .range_inputs>div{float:left;}
.daterangepicker .ranges .range_inputs>div:nth-child(2){padding-left:11px;}
.daterangepicker.opensleft .ranges,
.daterangepicker.opensleft .calendar{float:left;margin:4px;}
.daterangepicker .icon{width:20px;height:20px;display:inline-block;vertical-align:middle;}
/** bootstrap-daterangepicker **/
.isloading-overlay{position:relative;text-align:center;}
.isloading-overlay .isloading-wrapper{background:#FFFFFF;font-size:24px;-webkit-border-radius:7px;-webkit-background-clip:padding-box;-moz-border-radius:7px;-moz-background-clip:padding;border-radius:7px;background-clip:padding-box;display:inline-block;margin:0 auto;padding:10px 20px;top:10%;z-index:9000;}